import * as d3 from "d3-color";

const labs: {[key: string]: d3.LabColor} = {};

export function hexToLab(hex: string): d3.LabColor {
    let lab = labs[hex];
    if (!lab) {
        lab = d3.lab(hex);
        labs[hex] = lab;
    }
    return lab;
}

export function distance(clr1: string, clr2: string): number {
    const lab1 = hexToLab(clr1);
    // console.log("lab1 L:" + lab1.l);
    const lab2 = hexToLab(clr2);
    const lDiff = Math.pow(lab2.l - lab1.l, 2);
    const aDiff = Math.pow(lab2.a - lab1.a, 2);
    // console.log("lDiff: " + lDiff);
    const bDiff = Math.pow(lab2.b - lab1.b, 2);
    return Math.sqrt(lDiff + aDiff + bDiff);

    // const lab = Colour(hex).toLinearRGB().toXYZ().toCIELab();
    // return JSON.stringify(lab);
}

// export function rgbToHex(r: number, g: number, b: number): string {
//     if (r > 255 || g > 255 || b > 255)
//         throw "Invalid color component";
//     return "#" + ((r << 16) | (g << 8) | b).toString(16);
// }
function componentToHex(c: number): string {
    var hex = c.toString(16);
    return hex.length == 1 ? "0" + hex : hex;
}

export function rgbToHex(r: number, g: number, b: number): string {
    return "#" + componentToHex(r) + componentToHex(g) + componentToHex(b);
}


