import * as React from "react";
import * as fileio from "../util/fileio";
import * as colors from "../colors/colors";
import * as libraries from "../colors/libraries";
import * as d3 from "d3-color";
// Maybe someday use multi-page support
// import { Route, BrowserRouter } from "react-router-dom";
// import { createBrowserHistory } from "history";

// import {CalcPage} from "./CalcPage";

export interface AppState {
    imageLoaded: boolean;
    imagePixelized: boolean;
    width: number;
    height: number;
    canvasWidth: number;
    canvasHeight: number;
    aspectRatio: number;
    colorKey: Array<ColorCount>;
    displayGrid: boolean;
}

interface ColorCount {
    code: string;
    name: string;
    count: number;
}

const displayCanvasWidth = 4000;

/**
 * This is the wrapper application element. If this application
 * ever needs to support multiple pages, that
 * multi-page support would be configured here.
 */
export class App extends React.Component<undefined, AppState> {

    componentWillMount() {
        this.setState({
            imageLoaded: false,
            imagePixelized: false,
            width: 100,
            height: 100,
            canvasWidth: 100,
            canvasHeight: 100,
            aspectRatio: 1,
            displayGrid: true,
        });
    }

    updateWidth(evt: React.ChangeEvent<HTMLInputElement>) {
        const width = parseInt(evt.target.value);
        const height = Math.floor(width / this.state.aspectRatio);
        this.setState({
            width: parseInt(evt.target.value),
            height: height,
        });
    }

    updateHeight(evt: React.ChangeEvent<HTMLInputElement>) {
        const height = parseInt(evt.target.value);
        const width = Math.floor(height * this.state.aspectRatio);
        this.setState({
            height: parseInt(evt.target.value),
            width: width,
        });
    }

    private disableSmoothing(ctx: CanvasRenderingContext2D) {
        ctx.imageSmoothingEnabled = false;
        ctx.webkitImageSmoothingEnabled = false;
        ctx.mozImageSmoothingEnabled = false;
    }

    private sortCounts(counts: any): Array<ColorCount> {
        const result: Array<ColorCount> = [];
        Object.keys(counts).forEach(key => {
            const color = libraries.herrschners.getColor(key);
            result.push({
                code: color.code,
                name: color.name,
                count: counts[key],
            });
        });
        result.sort((a, b) => a.count - b.count);
        // Order by highest first
        result.reverse();
        return result;
    }

    pixelizeImage() {
        const ctx = this.canvas.getContext("2d");
        ctx.clearRect(0, 0, this.canvas.width, this.canvas.height);

        setTimeout(() => {
            this.setState({
                canvasWidth: this.state.width,
                canvasHeight: this.state.height,
            });
            let ctx = this.canvas.getContext("2d");
            this.disableSmoothing(ctx);
            ctx.drawImage(this.img, 0, 0, this.state.width, this.state.height);

            const imgData = ctx.getImageData(0, 0, this.state.width, this.state.height);
            const counts = {};
            const pixelCodes = [];
            for (let i = 0; i < imgData.data.length; i += 4) {

                const r = imgData.data[i];
                const g = imgData.data[i + 1];
                const b = imgData.data[i + 2];
                const hex = colors.rgbToHex(r, g, b);
                const closest = libraries.herrschners.closestMatch(hex);
                pixelCodes.push(closest.code);
                // console.log(closest.name);
                if (!counts[closest.code]) {
                    counts[closest.code] = 0;
                }
                counts[closest.code]++;
                const clr = d3.rgb(closest.code);
                imgData.data[i] = clr.r;
                imgData.data[i + 1] = clr.g;
                imgData.data[i + 2] = clr.b;
            }
            ctx.putImageData(imgData, 0, 0, 0, 0, this.state.width, this.state.height);
            // TODO: set state for key

            // draw small image on larger canvas
            const displayCanvasHeight = displayCanvasWidth / this.state.aspectRatio;
            ctx = this.displayCanvas.getContext("2d");
            ctx.clearRect(0, 0, this.displayCanvas.width, this.displayCanvas.height);

            this.disableSmoothing(ctx);
            ctx.drawImage(this.canvas, 0, 0, displayCanvasWidth, displayCanvasHeight);
            // draw gridlines
            ctx.strokeStyle = "#000000";
            const cellWidth = displayCanvasWidth / this.state.canvasWidth;
            const cellHeight = displayCanvasHeight / this.state.canvasHeight;

            
            

            // color key
            const colorKey = this.sortCounts(counts);

            if (this.state.displayGrid) {
                for (let x = 0; x < this.state.canvasWidth; x++) {
                    ctx.beginPath();
                    ctx.moveTo(x * cellWidth, 0);
                    ctx.lineTo(x * cellWidth, displayCanvasHeight);
                    ctx.stroke();
                }
                for (let y = 0; y < this.state.canvasHeight; y++) {
                    ctx.beginPath();
                    ctx.moveTo(0, y * cellHeight);
                    ctx.lineTo(displayCanvasHeight, y * cellHeight);
                    ctx.stroke();
                }
                // quickly map hex code to key number
                const colorKeyMap = {};
                colorKey.forEach((colorCount, i) => {
                    colorKeyMap[colorCount.code] = "" + (i + 1);
                });

                // draw numbers on each grid cell
                ctx.font = this.state.canvasWidth > 60 ? "24px Arial" : "36px Arial";

                for (let y = 0; y < this.state.canvasHeight; y++) {
                    for (let x = 0; x < this.state.canvasHeight; x++) {
                        const cellIndex = (y * this.state.canvasWidth) + x;
                        const cellHex = pixelCodes[cellIndex];
                        const displayKey = "" + colorKeyMap[cellHex];


                        ctx.fillStyle = "#000000";
                        ctx.strokeStyle = "#FFFFFF";

                        ctx.beginPath();
                        ctx.strokeText(displayKey, cellWidth * x + (cellWidth / 3), cellHeight * y + (cellHeight / 1.5));
                        ctx.fillText(displayKey, cellWidth * x + (cellWidth / 3), cellHeight * y + (cellHeight / 1.5));
                        ctx.stroke();
                    }
                }
            }



            // ctx.fillText("1", cellWidth / 2, cellHeight / 2);

            // Object.keys(counts).forEach(key => {
            //     console.log(key + ": " + counts[key]);
            // });
            this.setState({
                imagePixelized: true,
                colorKey: colorKey,
            });
        }, 100);
    }

    onOpenFile(evt: React.ChangeEvent<HTMLInputElement>) {
        this.setState({
            imageLoaded: false,
            imagePixelized: false,
            colorKey: null,
        });
        fileio.uploadFile(evt).then(data => {
            this.img.src = data;
            setTimeout(() => {
                const ratio = this.img.clientWidth / this.img.clientHeight;
                const width = 50;
                const height = width / ratio;
                this.setState({
                    imageLoaded: true,
                    imagePixelized: false,
                    width: width,
                    height: Math.floor(height),
                    aspectRatio: ratio,
                });
            }, 100);
        });
    }

    handleDisplayGridChange(evt: React.ChangeEvent<HTMLInputElement>) {
        this.setState({
            displayGrid: evt.target.checked,
        });
        if (this.state.imageLoaded) {
            setTimeout(() => {
                this.pixelizeImage();
            }, 100);
        }
        
    }

    /** Used to scale down image */
    private canvas: HTMLCanvasElement;
    /** Original image */
    private img: HTMLImageElement;
    private displayCanvas: HTMLCanvasElement;

    renderPixelizer() {
        if (this.state.imageLoaded) {
            return <form className="form-horizontal">
                <h4>Pixelize:</h4>
                <div className="form-group">
                    <label className="col-sm-2 control-label">Width:</label>
                    <div className="col-sm-3">
                        <input type="number" className="form-control" id="inputEmail3" placeholder="100"
                            value={this.state.width} onChange={this.updateWidth.bind(this)} />
                    </div>
                </div>
                <div className="form-group">
                    <label className="col-sm-2 control-label">Height:</label>
                    <div className="col-sm-3">
                        <input type="number" className="form-control" id="inputEmail3" placeholder="100"
                            value={this.state.height} onChange={this.updateHeight.bind(this)} />
                    </div>
                </div>
                <div className="form-group">
                    <div className="col-sm-offset-2 col-sm-3">
                        <button type="button" className="btn btn-primary" onClick={() => this.pixelizeImage()}>Go!</button>
                        &nbsp;
                        <input type="checkbox" checked={this.state.displayGrid} onChange={(evt) => this.handleDisplayGridChange(evt)} /> Display Grid
                    </div>
                </div>
            </form>
        }
    }

    renderCanvas() {
        if (this.state.imageLoaded) {
            const displayCanvasHeight = displayCanvasWidth / this.state.aspectRatio;
            const canvasStyle = this.state.imagePixelized ? { width: "100%" } : { display: "none" };
            return [<canvas
                className="pixelizer-canvas" style={{ display: "none" }} ref={ref => { this.canvas = ref }}
                width={this.state.canvasWidth} height={this.state.canvasHeight} />,
            <canvas className="pixelizer-canvas" style={canvasStyle} ref={ref => this.displayCanvas = ref}
                width={displayCanvasWidth} height={displayCanvasHeight} />];

        }
    }

    renderLoader() {
        // if (!this.state.imageLoaded) {
        return [<input type="file" onChange={evt => this.onOpenFile(evt)} />, <hr />];
        // }
    }

    renderColorKey() {
        if (this.state.colorKey) {
            return <div className="row">
                <div className="col-lg-12">
                    <div className="block-flat">
                        <div className="header">
                            <h3>Color Key</h3>
                        </div>
                        <div className="content">
                            <table>
                                <tr>
                                    <th>Key</th>
                                    <th>Color Name</th>
                                    <th>Count</th>
                                </tr>
                                {this.state.colorKey.map((colorInfo, i) => {
                                    const style = {
                                        background: colorInfo.code,
                                        color: colorInfo.code,
                                    };
                                    return <tr key={colorInfo.code}>
                                        <td>{i + 1}</td>
                                        <td><span style={style}>___</span>&nbsp;&nbsp;{colorInfo.name}</td>
                                        <td>{colorInfo.count}</td>
                                    </tr>
                                })};
                        </table>
                        </div>
                    </div>
                </div>

            </div>
        }
    }

    render() {
        const imageStyle = this.state.imagePixelized ? { display: "none" } : { width: "100%" };
        return <div className="cl-mcont">
            {/* <CalcPage /> */}
            <div className="row">
                <div className="col-lg-12">
                    <div className="block-flat">
                        <div className="header">
                            <h3>Pixelizer</h3>
                        </div>
                        <div className="content">
                            {this.renderLoader()}
                            {this.renderPixelizer()}
                            <img ref={ref => this.img = ref} style={imageStyle} />
                            {this.renderCanvas()}
                        </div>
                    </div>
                </div>

            </div>
            {this.renderColorKey()}
        </div>
    }
}


