import * as d3 from "d3-color";
import * as colors from "./colors";

export interface LibraryColor {
    code: string;
    name: string;
}

export class ColorLibrary {

    constructor(private colorMap: {[key: string]: string}) {
        
    }

    closestMatch(hexColor: string): LibraryColor {
        let bestCode = "#FFFFFF";
        let bestDiff = colors.distance(hexColor, bestCode);
        // console.log("Best diff: " + bestDiff);
        Object.keys(this.colorMap).forEach(libCode => {
            const diff = colors.distance(hexColor, libCode);
            if (diff < bestDiff) {
                bestDiff = diff;
                bestCode = libCode;
            }
        });
        return {
            code: bestCode,
            name: this.colorMap[bestCode],
        };
    }

    getColor(hexColor: string): LibraryColor {
        return {
            code: hexColor,
            name: this.colorMap[hexColor],
        };
    }

}

